.header {
  margin-bottom: 60px;
  background-image: url(../images/back-header.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom 0 right 10%;

  @include onTablet {
    margin-bottom: 120px;
    background-image: url(../images/back-header-tablet.png);
  }

  @include onDesktop {
    background-position: center;
    background-image: url(../images/back-header.png);
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    padding: 28px 0 88px;

    @include onTablet {
      padding: 34px 0 130px;
    }

    @include onDesktop {
      padding: 36px 0 202px;
    }
  }

  &__top {
    @include top-actions;
  }

  &__title {
    margin: 0 auto;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    letter-spacing: -1.5px;
    color: $c-white;

    @include onTablet {
      font-size: 64px;
    }

    @include onDesktop {
      font-size: 80px;
    }
  }

  &__action {
    display: flex;
  }
}
